:root {
    --green-color-1: white;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    padding: 4px 64px;
    background-color: transparent;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 10;
  
    transition: 0.4s ease;
}
  
.container a {
    text-decoration: none;
}
  
.container ul {
    padding: 0;
    list-style: none;
}

/* title */
.title {
    display: flex;
    align-items: center;
}
.title i {
    font-size: 32px;
    margin: 0 8px 0 0;
}
.title strong {
    font-size: 32px;
    letter-spacing: 0.5px;
}

/* 아이콘 */
.icon__list {
    display: flex;
}
  
.icon__list li {
    font-size: 24px;
    padding: 4px 8px;
}
  
.icon__list li a {
    color: var(--green-color-1);
}
  
.null {
    width: 100%;
    height: 100px;
}
  
/* 미디어쿼리 */
@media screen and (max-width: 1090px) {
    .option__list {
      display: none;
    }
}
